import React from 'react'
import { Link } from "react-router-dom";
import $ from 'jquery'; 
import { useState } from 'react';
const Login = () => {


   // Initialize a boolean state
   const [passwordShown, setPasswordShown] = useState(false);

   // Password toggle handler
   const togglePassword = () => {
     // When the handler is invoked
     // inverse the boolean state of passwordShown
     setPasswordShown(!passwordShown);
   };

    
  return (


    <body class="graphic-bg" >
    <div class="log-reg-section clearfix">
  <div class="row g-0">
    <div class="col-lg-7 col-md-6 order-lg-2 order-md-2">
      <div class="right-panel">
        <div class="logo"><a href="index.html">
        <img src="assets/img/log-reg-logo.png" alt=""/>
        </a></div>
        <div class="field-box">
          <label>Email Address</label>
          <input type="email" class="input-email" placeholder="Enter your email address"/>
        </div>
        <div class="field-box">
          <label>Password</label>
          <input id="password-field"  type={passwordShown ? "text" : "password"} placeholder="Enter your password"/>
          <span onClick={togglePassword} toggle="#password-field" style={{cursor:'pointer'}} 
          class= { passwordShown ? "fa fa-fw field-icon toggle-password fa-eye-slash" : "fa fa-fw field-icon toggle-password fa-eye" }
           ></span> </div>
        <div class="forgot-password"><a href="#">Forgot Password?</a></div>
        <div class="field-box  LoginNow">
         <button type="submit">
          Login Now
        </button>
         {/* <input type="submit" value="Login Now"/> */}
        </div>
        <div class="field-box">
        <Link style={{"color": "#0BC7B6" ,border: "2px solid #0BC7B6" ,padding:"10px 36px" , borderRadius: "8px" ,fontWeight:'bold' }} to="/select-user">Register Now</Link>
       
          {/* <button type="button"> <Link style={{"color": "#0BC7B6"}} to="/select-user">Register Now</Link></button> */}
       
        </div>
        <div class="copyrights">All Copyrights Reserved by <a style={{fontWeight:"bold"}} href="#">MORPH CO. LTD</a></div>
      </div>
    </div>
    <div class="col-lg-5 col-md-6">
      <div class="left-panel">
        <div class="back-to-home"><i style={{marginRight:"5px"}} class="fa-solid fa-arrow-left-long"></i>
        <Link  to="/">Back to Home</Link>
         </div>        
        <img src="assets/img/login-img.png" alt=""/></div>
    </div>
  </div>
</div>



    </body>
  )
}

export default Login



